<template>
  <div class="row" style="margin-top: 50px;margin-bottom: 20px; padding-bottom:15px; border:1px solid #2f3542;">
    <div class="product_box">
      <img src="/static/img/pete.png" width="100px" height="100px" class="img-thumbnail">

      <div class="information_box">
        <h4><strong>Ürün Ad:</strong> {{ product.name }}</h4>
        <h4><strong>Barkod:</strong> {{ product.barcode }}</h4>
        <h4><strong>KDV:</strong> % {{ product.tax }}</h4>
      </div>
    </div>

    <div class="right">
      <div class="value_table">
        <table  class="tablo" border=2 bordercolor=#e17055  border-radius=5px>
          <tr>
            <td>Fiy.Tipi</td>
            <td>	Brm	 </td>
            <td>	Koli.Fiy. </td>
            <td>Adt.	Fiy. </td>
            <td>Ks Brm Fiy.</td>
            <td>İçer.</td>
          </tr>

          <tr v-for="(productSpec,index) in product.productSpecifies" :key="`prodSpec-${index}`">
            <td> {{ productSpec.user.name }}</td>
            <td> {{ productSpec.unitType }}</td>
            <td> {{ productSpec.totalPrice }}</td>
            <td> {{ productSpec.unitPrice }}</td>
            <td> {{ productSpec.contents }}</td>
            <td> {{ productSpec.quantity }}</td>
          </tr>

        </table>
      </div>
      <div class="value_box">
        <input type="number" min="0" max="100"/>
        <button type="button" class="add_repository" width="100px">Depo Ekle</button><br><br>
        <input type="number" min="0" max="100"/>
        <button type="button" class="add_repository" width="100px">Kasa Fiyatı Kaydet</button>
      </div>
    </div>
  </div>

</template>

<script>
    export default {
        name: "Product",
      props:{
          product: Object
      }
    }
</script>

<style scoped>

</style>



